import React from "react";
import s from './Basket.module.css'
import Product from "./Product/Product";
import image1 from './../img/image38.png'
import image2 from './../img/Vector2.png'


const  Basket = (props) => {
    return (
        <div className={s.Basket}>
            <div className={s.BasketProduct}>
                <div className={s.BasketDelivery}>
                    <div className={s.BasketDeliveryCity}>из Санкт-Петербурга</div>
                    <div className={s.BasketDeliveryInfo} >
                        <span className={s.BasketDeliveryDate} >Поступление 27-29 июля за 350 ₽</span>
                        <img className={s.BasketDeliveryLogo} src={image1}  />
                        <img className={s.BasketDeliveryVector} src={image2}  />
                    </div>
                </div>
                <Product />
            </div>

        </div>
    )
}
export default Basket;